/* global location */
/* eslint no-restricted-globals: ["off", "location"] */
import { handleResponse} from '../_helpers/utils';
import {
    authHeader,
    url,
    requestOptions
} from '../_helpers';

export const userService = {
    login,
    logout,
    register,
    getAll,
    getById,
    update,
    delete: _delete,
    getUserLogin,
    loginOthers
};

function login(email, senha) {
    const request = requestOptions('POST', JSON.stringify({
        email,
        senha
    }));

    return fetch(url + '/users/autentication', request)
        .then(handleResponse)
        .then(user => {
            // login successful if there's a jwt token in the response
            if (user.token) {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                localStorage.setItem('user', JSON.stringify(user));
            }

            return user;
        });
}

function loginOthers(json) {
    const request = requestOptions("POST", json);

    return fetch(url + '/users/autenticationOthers', request)
        .then(handleResponse)
        .then(user => {
            // login successful if there's a jwt token in the response
            if (user.token) {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                localStorage.setItem('user', JSON.stringify(user));
            }

            return user;
        });
}

function logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('user');
}

function getAll() {
    const request = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(url + '/users', request).then(handleResponse);
}

function getById(id) {
    const request = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(url + '/users/' + id, request).then(handleResponse);
}

function getUserLogin() {
    return localStorage.getItem('user');
}

function register(user) {
    const request = requestOptions("POST",
        user
    );
    //console.log(JSON.stringify(user));
    return fetch(url + '/users/register', request).then(handleResponse);
}

function update(user) {
    const request = requestOptions("PUT",
        user
    );

    return fetch(url + '/users/' + user.id, request).then(handleResponse);
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    const request = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(url + '/users/' + id, request).then(handleResponse);
}
