import React from "react";
import PropTypes from "prop-types";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Timeline from "@material-ui/icons/Timeline";
import Code from "@material-ui/icons/Code";
import Group from "@material-ui/icons/Group";
import Face from "@material-ui/icons/Face";
import Email from "@material-ui/icons/Email";

import Check from "@material-ui/icons/Check";
import { connect } from 'react-redux';
import compose from 'recompose/compose';

// core components
import GridContainer from "./../../components/Grid/GridContainer.jsx";
import GridItem from "./../../components/Grid/GridItem.jsx";
import Button from "./../../components/CustomButtons/Button.jsx";
import CustomInput from "./../../components/CustomInput/CustomInput.jsx";
import InfoArea from "./../../components/InfoArea/InfoArea.jsx";
import Card from "./../../components/Card/Card.jsx";
import CardBody from "./../../components/Card/CardBody.jsx";
import { userActions } from '../../_actions/user.actions';
import registerPageStyle from "./../../assets/jss/material-dashboard-pro-react/views/registerPageStyle";
import GoogleLogin from 'react-google-login';

class RegisterPage extends React.Component {
  constructor(props) {
    super(props);
    this.initialState = {
      confirmaTermo: false,
      nome: '',
      email: '',
      senha: '',
      confirmaSenha: '',
      foto: ''
    };

    this.state = this.initialState;

    //this.handleToggle = this.handleToggle.bind(this);
    this.validate = this.validate.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  /*
    handleToggle(value) {
      const { checked } = this.state;
      const currentIndex = checked.indexOf(value);
      const newChecked = [...checked];
  
      if (currentIndex === -1) {
        newChecked.push(value);
      } else {
        newChecked.splice(currentIndex, 1);
      }
  
      this.setState({
        checked: newChecked
      });
    }*/

  componentWillReceiveProps(nextProps) {
    if (nextProps.alert !== this.props.alert) {
      if (nextProps.alert.type == 'success') this.setState(() => this.initialState);
    }
  }


  handleSubmit(e) {
    e.preventDefault();
    const { nome, email, senha, confirmaSenha } = this.state;
    const { dispatch } = this.props;

    //var seqconta = authentication.user.seqconta;

    if (this.isInvalid() == false) {
      dispatch(userActions.register(JSON.stringify({ nome, email, senha, confirmaSenha })));
    }
  }

  handleChange(e) {
    const { name, value, checked, id } = e.target;

    this.validate(e);

    if (id === 'confirmaTermo') {
      this.setState({ [id]: checked });
    } else {
      this.setState({ [name]: value });
    }
  }

  validate(e) {

    const { name, value, checked, id } = e.target;
    var form = e.target.parentElement.parentElement.parentElement;

    if (!value || (name === 'nome' && value.length == 0)) {
      return this.setState({ [`${name}Error`]: true, [`${name}Message`]: "Campo obrigatrio" });
    }

    if (!value || (name === 'email' && value.length == 0)) {
      return this.setState({ [`${name}Error`]: true, [`${name}Message`]: "Campo obrigatrio" });
    }

    if (!value || (name === 'senha' && value.length == 0)) {
      return this.setState({ [`${name}Error`]: true, [`${name}Message`]: "Campo obrigatrio" });
    }

    if (name === 'confirmaSenha' && value.length == 0) {
      return this.setState({ [`${name}Error`]: true, [`${name}Message`]: "Campo obrigatrio" });
    }

    if (form.querySelector("#senha").value !== form.querySelector("#confirmaSenha").value) {
      return this.setState({ "confirmaSenhaError": true, "confirmaSenhaMessage": "Campo confirmao de senha diferente do campo senha" });
    }

    if (id === 'confirmaTermo' && !checked) {
      return this.setState({ [`${id}Error`]: true, [`${id}Message`]: "Campo obrigatrio" });
    }

    this.setState({ [`${name}Error`]: false, [`${name}Message`]: null });

  }

  isInvalid = () => {

    const { nome, email, senha, confirmaSenha, confirmaTermo, nomeError, emailError, senhaError, confirmaSenhaError, confirmaTermoError } = this.state;

    return !(
      nome &&
      email &&
      senha &&
      confirmaSenha &&
      confirmaTermo &&
      !nomeError &&
      !emailError &&
      !senhaError &&
      !confirmaSenhaError &&
      !confirmaTermoError
    );
  }

  voltar() {
    this.props.history.push("/pages/login-page");
  }

  render() {
    const { classes } = this.props;
    const { nome, email, senha, confirmaTermo, confirmaSenha } = this.state;

    return (
      <div className={classes.container} id={'section1'}>
        <GridContainer justify="center">
          <GridItem xs={12} sm={12} md={10}>
            <Card className={classes.cardSignup}>
              <h2 className={classes.cardTitle}>Registro</h2>
              <CardBody>
                <GridContainer justify="center">
                  <GridItem xs={12} sm={12} md={5}>
                    <InfoArea
                      title="Movimentaes"
                      description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus aliquet mattis consequat."
                      icon={Timeline}
                      iconColor="rose"
                    />
                    <InfoArea
                      title="Conta Corrente"
                      description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus aliquet mattis consequat."
                      icon={Code}
                      iconColor="primary"
                    />
                    <InfoArea
                      title="Gerenciamento"
                      description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus aliquet mattis consequat."
                      icon={Group}
                      iconColor="info"
                    />
                  </GridItem>
                  <GridItem xs={12} sm={8} md={5}>
                    <div className={classes.center}>
                      <Button justIcon round color="google">
                        <i className="fab fa-google" />
                      </Button>
                      {` `}
                      {/* <Button justIcon round color="dribbble">
                        <i className="fab fa-dribbble" />
                      </Button>
                      {` `} */}
                      <Button justIcon round color="facebook">
                        <i className="fab fa-facebook-f" />
                      </Button>
                      {` `}
                      <h4 className={classes.socialTitle}>ou </h4>
                    </div>
                    <form className={classes.form} name="form" onSubmit={this.handleSubmit} ref="form">
                      <CustomInput
                        formControlProps={{
                          fullWidth: true,
                          className: classes.customFormControlClasses
                        }}
                        onChange={this.handleChange}
                        onBlur={this.validate}
                        id="nome"
                        value={nome}
                        required={true}
                        name="nome"
                        error={this.state.nomeError}
                        inputProps={{
                          startAdornment: (
                            <InputAdornment
                              position="start"
                              className={classes.inputAdornment}
                            >
                              <Face className={classes.inputAdornmentIcon} />
                            </InputAdornment>
                          ),
                          placeholder: "Nome"
                        }}
                        helpText={this.state.nomeMessage}
                      />
                      <CustomInput
                        formControlProps={{
                          fullWidth: true,
                          className: classes.customFormControlClasses
                        }}
                        id="email"
                        required={true}
                        value={email}
                        name="email"
                        onChange={this.handleChange}
                        onBlur={this.validate}
                        error={this.state.emailError}
                        helpText={this.state.emailMessage}
                        inputProps={{
                          startAdornment: (
                            <InputAdornment
                              position="start"
                              className={classes.inputAdornment}
                            >
                              <Email className={classes.inputAdornmentIcon} />
                            </InputAdornment>
                          ),
                          placeholder: "Email"
                        }}
                      />
                      <CustomInput
                        formControlProps={{
                          fullWidth: true,
                          className: classes.customFormControlClasses
                        }}
                        id="senha"
                        required={true}
                        value={senha}
                        name="senha"
                        type="password"
                        error={this.state.senhaError}
                        onChange={this.handleChange}
                        onBlur={this.validate}
                        inputProps={{
                          startAdornment: (
                            <InputAdornment
                              position="start"
                              className={classes.inputAdornment}
                            >
                              <Icon className={classes.inputAdornmentIcon}>
                                lock_outline
                              </Icon>
                            </InputAdornment>
                          ),
                          placeholder: "Senha"
                        }}
                        helpText={this.state.senhaMessage}
                      />
                      <CustomInput
                        formControlProps={{
                          fullWidth: true,
                          className: classes.customFormControlClasses
                        }}
                        id="confirmaSenha"
                        required={true}
                        value={confirmaSenha}
                        name="confirmaSenha"
                        type="password"
                        onChange={this.handleChange}
                        onBlur={this.validate}
                        error={this.state.confirmaSenhaError}
                        inputProps={{
                          startAdornment: (
                            <InputAdornment
                              position="start"
                              className={classes.inputAdornment}
                            >
                              <Icon className={classes.inputAdornmentIcon}>
                                lock_outline
                              </Icon>
                            </InputAdornment>
                          ),
                          placeholder: "Confirmao Senha"
                        }}
                        helpText={this.state.confirmaSenhaMessage}
                      />
                      <FormControlLabel
                        classes={{
                          root: classes.checkboxLabelControl,
                          label: classes.checkboxLabel
                        }}
                        control={
                          <Checkbox
                            id="confirmaTermo"
                            required={true}
                            checked={confirmaTermo}
                            error={this.state.confirmaTermoError}
                            onChange={this.handleChange}
                            onBlur={this.validate}
                            tabIndex={-1}
                            // onClick={() => this.handleToggle(1)}
                            checkedIcon={
                              <Check className={classes.checkedIcon} />
                            }
                            icon={<Check className={classes.uncheckedIcon} />}
                            classes={{
                              checked: classes.checked
                            }}
                            helpText={this.state.confirmaTermoMessage}
                          />
                        }
                        label={
                          <span>
                            Eu aceito {" "}
                            <a href="#pablo">os termos e condies</a>.
                          </span>
                        }
                      />
                      <div className={classes.center}>
                        <Button round color="info" onClick={() => { this.voltar(); }}>
                          Voltar
                        </Button>
                        <Button type="submit" round color="primary" disabled={this.isInvalid()}>
                          Cadastrar
                        </Button>
                      </div>
                    </form>
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

RegisterPage.propTypes = {
  classes: PropTypes.object.isRequired
};


const mapStateToProps = (state) => {
  return {
    alert: state.alert,
  }
}

export default compose(
  connect(mapStateToProps),
  withStyles(registerPageStyle)
)(RegisterPage);

