import React from "react";
import { connect } from 'react-redux';
import compose from 'recompose/compose';
import PropTypes from "prop-types";
// react plugin for creating charts
import ChartistGraph from "react-chartist";
// react plugin for creating vector maps
import { VectorMap } from "react-jvectormap";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Tooltip from "@material-ui/core/Tooltip";
import Icon from "@material-ui/core/Icon";

// @material-ui/icons
// import ContentCopy from "@material-ui/icons/ContentCopy";
import Store from "@material-ui/icons/Store";
// import InfoOutline from "@material-ui/icons/InfoOutline";
import Warning from "@material-ui/icons/Warning";
import DateRange from "@material-ui/icons/DateRange";
import LocalOffer from "@material-ui/icons/LocalOffer";
import Update from "@material-ui/icons/Update";
import ArrowUpward from "@material-ui/icons/ArrowUpward";
import AccessTime from "@material-ui/icons/AccessTime";
import Refresh from "@material-ui/icons/Refresh";
import Edit from "@material-ui/icons/Edit";
import Place from "@material-ui/icons/Place";
import ArtTrack from "@material-ui/icons/ArtTrack";
import Language from "@material-ui/icons/Language";

// core components
import GridContainer from "./../../components/Grid/GridContainer.jsx";
import GridItem from "./../../components/Grid/GridItem.jsx";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Button from "./../../components/CustomButtons/Button.jsx";
import Danger from "./../../components/Typography/Danger.jsx";
import Card from "./../../components/Card/Card.jsx";
import CardHeader from "./../../components/Card/CardHeader.jsx";
import CardIcon from "./../../components/Card/CardIcon.jsx";
import CardBody from "./../../components/Card/CardBody.jsx";
import CardFooter from "./../../components/Card/CardFooter.jsx";

import { dashboardActions } from '../../_actions/dashboard.actions';

import {
  dailySalesChart,
  emailsSubscriptionChart,
  completedTasksChart
} from "./../../variables/charts";
import dashboardStyle from "./../../assets/jss/material-dashboard-pro-react/views/dashboardStyle";

import "moment/locale/pt-br";
var moment = require('moment');
moment.locale('pt-br');

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    width: '100%',
  },
  ...dashboardStyle
});

class Dashboard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      loadingfluxoAteHoje: false,
      loadingSaldo: false,
      loadingConciliacaoPendente: false,
      loadingFluxo: false,
      loadingDispPorConta: false,
      loadingNatureza: false,
      loadingCentroCusto: false,
      loadingGrupo: false,
      fluxoAteHoje: 0,
      saldo: 0,
      conciliacaoPendente: 0,
      fluxo: 0,
      DispPorConta: [],
      natureza: [],
      centrocusto: [],
      grupo: [],
      error: null
    };

    this.carregaDash();
  }

  carregaDash() {

    const { dispatch, authentication } = this.props;

    var seqconta = authentication.user.seqconta;

    dispatch(dashboardActions.getFluxoAteHoje(seqconta));
    dispatch(dashboardActions.getSaldo(seqconta));
    dispatch(dashboardActions.getConciliacaoPendente(seqconta));
    dispatch(dashboardActions.getFluxo(seqconta));
    dispatch(dashboardActions.getDisponibilidadePorConta(seqconta));
    dispatch(dashboardActions.getNatureza(seqconta));
    dispatch(dashboardActions.getCentroCusto(seqconta));
    dispatch(dashboardActions.getGrupo(seqconta));

  }


  componentWillReceiveProps(nextProps) {
    if (nextProps.dashboard !== this.props.dashboard) {
      //console.log(nextProps);
      this.setState({
        loadingfluxoAteHoje: nextProps.dashboard.loadingfluxoAteHoje,
        loadingSaldo: nextProps.dashboard.loadingSaldo,
        loadingConciliacaoPendente: nextProps.dashboard.loadingConciliacaoPendente,
        loadingFluxo: nextProps.dashboard.loadingFluxo,
        loadingNatureza: nextProps.dashboard.loadingDispPorConta,
        loadingCentroCusto: nextProps.dashboard.loadingCentroCusto,
        loadingGrupo: nextProps.dashboard.loadingGrupo,
        fluxoAteHoje: (nextProps.dashboard.fluxoAteHoje) ? nextProps.dashboard.fluxoAteHoje.fluxoatehoje.toLocaleString('pt-br', { minimumFractionDigits: 2 }) : '0,00',
        saldo: (nextProps.dashboard.saldo) ? nextProps.dashboard.saldo.saldo.toLocaleString('pt-br', { minimumFractionDigits: 2 }) : '0,00',
        conciliacaoPendente: (nextProps.dashboard.conciliacaoPendente) ? nextProps.dashboard.conciliacaoPendente.qtde.toLocaleString('pt-br', { minimumFractionDigits: 0 }) : "0",
        fluxo: (nextProps.dashboard.fluxo) ? nextProps.dashboard.fluxo.fluxo.toLocaleString('pt-br', { minimumFractionDigits: 2 }) : "0,00",
        DispPorConta: nextProps.dashboard.DispPorConta,
        natureza: nextProps.dashboard.natureza,
        centrocusto: nextProps.dashboard.centrocusto,
        grupo: nextProps.dashboard.grupo
      });
    }
  }

  carregaDispPorConta() {
    const { classes } = this.props;
    if (this.state.DispPorConta != undefined) {
      if (this.state.DispPorConta.length > 0) {
        var total = 0;
        return (
          <Table className={classes.table}>
            <TableBody>
              {this.state.DispPorConta.map((prop, key) => {
                total += prop.saldo;
                return (
                  <TableRow key={key}>
                    <TableCell>{prop.descricao}</TableCell>
                    <TableCell rowSpan={1} />
                    <TableCell numeric><span style={(prop.saldo < 0) ? { color: "red" } : { color: "" }}>{prop.saldo.toLocaleString('pt-br', { minimumFractionDigits: 2 })}</span> </TableCell>
                  </TableRow>
                );
              })}
              <TableRow>
                <TableCell rowSpan={1} />
                <TableCell>Total</TableCell>
                <TableCell numeric><span style={(total < 0) ? { color: "red" } : { color: "" }} >{total.toLocaleString('pt-br', { minimumFractionDigits: 2 })}</span></TableCell>
              </TableRow>
            </TableBody>
          </Table>);
      }
    }
    return (
      <Table className={classes.table} >
        <TableBody>
          <TableRow>
            <TableCell colSpan={3}>Nenhuma Informao encontrada</TableCell>
          </TableRow>
        </TableBody>
      </Table>);
  }

  carregaTodos(state) {
    const { classes } = this.props;
    if (state != undefined) {
      if (state.length > 0) {
        var total = 0;
        return (
          <Table className={classes.table}>
            <TableBody>
              {state.map((prop, key) => {
                total += prop.vlroriginal;
                return (
                  <TableRow key={key}>
                    <TableCell>{prop.descricao}</TableCell>
                    <TableCell rowSpan={1} />
                    <TableCell numeric><span style={(prop.vlroriginal < 0) ? { color: "red" } : { color: "" }}>{prop.vlroriginal.toLocaleString('pt-br', { minimumFractionDigits: 2 })}</span> </TableCell>
                  </TableRow>
                );
              })}
              <TableRow>
                <TableCell rowSpan={1} />
                <TableCell>Total</TableCell>
                <TableCell numeric><span style={(total < 0) ? { color: "red" } : { color: "" }} >{total.toLocaleString('pt-br', { minimumFractionDigits: 2 })}</span></TableCell>
              </TableRow>
            </TableBody>
          </Table>);
      }
    }
    return (
      <Table className={classes.table} >
        <TableBody>
          <TableRow>
            <TableCell colSpan={3}>Nenhuma Informao encontrada</TableCell>
          </TableRow>
        </TableBody>
      </Table>);
  }

  render() {
    const { classes } = this.props;
    const { fluxoAteHoje, saldo, conciliacaoPendente, fluxo, DispPorConta, natureza, centrocusto, grupo } = this.state;

    return (
      <div>
        <GridContainer>
          <GridItem xs={12} sm={6} md={6} lg={3}>
            <Card>
              <CardHeader color="warning" icon>
                <CardIcon color="warning">
                  <Icon>trending_up</Icon>
                </CardIcon>
                <p className={classes.cardCategory}>Fluxo at hoje</p>
                <h3 className={classes.cardTitle}>
                  <small>R$</small> {fluxoAteHoje}
                </h3>
              </CardHeader>
              {/* <CardFooter stats>
                <div className={classes.stats}>
                  <Danger>
                    <Warning />
                  </Danger>
                  <a href="#pablo" onClick={e => e.preventDefault()}>
                    Get more space
                  </a>
                </div>
              </CardFooter> */}
            </Card>
          </GridItem>
          <GridItem xs={12} sm={6} md={6} lg={3}>
            <Card>
              <CardHeader color="success" icon>
                <CardIcon color="success">
                  <Icon>attach_money</Icon>
                </CardIcon>
                <p className={classes.cardCategory}>Saldo</p>
                <h3 className={classes.cardTitle}>
                  <small>R$</small> {saldo}</h3>
              </CardHeader>
              {/* <CardFooter stats>
                <div className={classes.stats}>
                  <DateRange />
                  Last 24 Hours
                </div>
              </CardFooter> */}
            </Card>
          </GridItem>
          <GridItem xs={12} sm={6} md={6} lg={3}>
            <Card>
              <CardHeader color="danger" icon>
                <CardIcon color="danger">
                  <Icon>info_outline</Icon>
                </CardIcon>
                <p className={classes.cardCategory}>Conciliao Pendente</p>
                <h3 className={classes.cardTitle}>
                  <small>Qtde: </small> {conciliacaoPendente}</h3>
              </CardHeader>
              {/* <CardFooter stats>
                <div className={classes.stats}>
                  <LocalOffer />
                  Tracked from Github
                </div>
              </CardFooter> */}
            </Card>
          </GridItem>
          <GridItem xs={12} sm={6} md={6} lg={3}>
            <Card>
              <CardHeader color="info" icon>
                <CardIcon color="info">
                  <Icon>show_chart</Icon>
                </CardIcon>
                <p className={classes.cardCategory}>Fluxo</p>
                <h3 className={classes.cardTitle}><small>R$</small> {fluxo}</h3>
              </CardHeader>
              {/* <CardFooter stats>
                <div className={classes.stats}>
                  <Update />
                  Just Updated
                </div>
              </CardFooter> */}
            </Card>
          </GridItem>
        </GridContainer>
        <GridContainer>
          <GridItem xs={12} sm={12} md={6}>
            <Card>
              <CardHeader color="rose">
                {/* <CardIcon color="success">
                  <Language />
                </CardIcon> */}
                <h4 className={classes.cardIconTitle}>
                  DISPONIBILIDADE POR CONTA
                </h4>
              </CardHeader>
              <CardBody>
                <GridContainer justify="space-between">
                  <GridItem xs={12} sm={12} md={12}>
                    {this.carregaDispPorConta()}
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Card>
          </GridItem>
          <GridItem xs={12} sm={12} md={6}>
            <Card>
              <CardHeader color="rose">
                {/* <CardIcon color="success">
                  <Language />
                </CardIcon> */}
                <h4 className={classes.cardIconTitle}>
                  GRUPO
                </h4>
              </CardHeader>
              <CardBody>
                <GridContainer justify="space-between">
                  <GridItem xs={12} sm={12} md={12}>
                    {this.carregaTodos(grupo)}
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
        <GridContainer>
          <GridItem xs={12} sm={12} md={6}>
            <Card>
              <CardHeader color="rose">
                {/* <CardIcon color="success">
                  <Language />
                </CardIcon> */}
                <h4 className={classes.cardIconTitle}>
                  CENTRO CUSTO
                </h4>
              </CardHeader>
              <CardBody>
                <GridContainer justify="space-between">
                  <GridItem xs={12} sm={12} md={12}>
                    {this.carregaTodos(centrocusto)}
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Card>
          </GridItem>

          <GridItem xs={12} sm={12} md={6}>
            <Card>
              <CardHeader color="rose">
                {/* <CardIcon color="success">
                  <Language />
                </CardIcon> */}
                <h4 className={classes.cardIconTitle}>
                  NATUREZA
                </h4>
              </CardHeader>
              <CardBody>
                <GridContainer justify="space-between">
                  <GridItem xs={12} sm={12} md={12}>
                    {this.carregaTodos(natureza)}
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Card>
          </GridItem>

        </GridContainer>
      </div>
    );
  }
}

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired
};


const mapStateToProps = (state) => {
  return {
    dashboard: state.dashboard,
    alert: state.alert,
    authentication: state.authentication.user
  }
}

export default compose(
  connect(mapStateToProps),
  withStyles(styles)
)(Dashboard);


