import { alertConstants } from '../_constants';

const initialState = {
  open: false,
  message: '',
  error: '',
  type: ''
}

export function alert(state = initialState, action) { 
  switch (action.type) {
    case alertConstants.SUCCESS:
      return {
        type: 'success',
        message: action.message || action.error,
        open: true
      };
    case alertConstants.ERROR:
      return {
        type: 'danger',
        message: action.message || action.error,
        open: true
      };
    case alertConstants.CLEAR:
      return {
        type: '',
        open: false,
        message: '',
        error: ''};
    default:
      return state
  }
}