import { alertConstants } from '../_constants';

export const alertActions = {
    success,
    error,
    clear
};

function success(message, open) {
    return { type: alertConstants.SUCCESS, message, open };
}

function error(message, open) {
    return { type: alertConstants.ERROR, message, open };
}

function clear() {
    return { type: alertConstants.CLEAR };
}