import React from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { compose } from 'redux';
import FacebookAuth from 'react-facebook-auth';
import withStyles from "@material-ui/core/styles/withStyles";
import PropTypes from "prop-types";
import Button from "./../CustomButtons/Button.jsx";
import {userActions} from './../../_actions/user.actions.js';
var base64Img = require('base64-img');

const style = theme => ({
  customButtonClass: {
    "&,&:focus,&:hover": {
      color: "#FFFFFF"
    },
    marginLeft: "5px",
    marginRight: "5px"
  }
});


function FaceAuth({ ...props }) {
  const { classes, color, key } = props;

  const MyFacebookButton = ({ onClick }) => (
    <Button onClick={onClick} color={color} key={key} justIcon className={classes.customButtonClass}>
      <i className="fab fa-facebook-square" />
    </Button>
  );

  const authenticate = (response) => {
      if(response){
        if (response.email) {
          var email = response.email;
          var foto = null;
          props.dispatch(userActions.loginOthers(JSON.stringify({email, foto})));
        }
      }
  };

  return (

    <FacebookAuth
      appId="188622242070930"
      callback={authenticate}
      component={MyFacebookButton}
      reAuthenticate={true}
    />
  );
}

FaceAuth.propTypes = {
  classes: PropTypes.object.isRequired
};
const mapStateToProps = (state) => {
  return { alert: state.alert, classes: state.authentication }
}

export default compose(
  connect(mapStateToProps),
  withStyles(style)
)(FaceAuth);